.copyright {
  text-align: center;
  padding-bottom: 31px;
}

.copyright__text {
  font-size: 19px;
  font-weight: bold;
  line-height: 18px;
  color: $color-black;
}

@media (min-width: $tablet-width) {
  .copyright {
    padding-bottom: 0;
  }
}

@media (min-width: $desktop-width) {
  .copyright {
    margin-left: 84px;
  }
}
